            text: 'Zajƒôcia na rowerach  sƒÖ to zajƒôcia grupowe, prowadzone przy odpowiedniej muzyce do wskaz√≥wek instruktora. Dziƒôki swoim nieskomplikowanym ruchom,mo≈ºe je wykonywac ka≈ºdy bez wzglƒôdu na wiek, p≈Çeƒá czy stopie≈Ñ wytrenowania. Jest to najbezpieczniejsza forma treningu wszystkich uk≈Çad√≥w, doskona≈Ça zar√≥wno dla rehabilitacji, profilaktyki jak i treningu sportowego. Odpowiednio dobrana muzyka nie tylko reguluje tempo jazdy, ale pozwala zapomnieƒá o wykonywanym wysi≈Çku. Czas trwania treningu oko≈Ço 50 min.',
